import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import Parking from "../../img/Parking.jpg";

const carouselTwoStyle={
    container:{
        margin:"2.8rem auto",
        backgroundColor:"black"
    },
    col:{
        margin:"4rem 1rem 4rem 1rem",
        padding:"0.9rem",
    },
    floorPlan:{
        margin:"3rem 2rem",
        textAlign:"center"
    },
    parkingDiv:{
        textAlign:"center"
    },
    parking:{
        margin:"3rem auto"
    }
}

function Carousel2(){
    return(
        <Container style={carouselTwoStyle.container} fluid>
            <Row>
                <Col style={carouselTwoStyle.col}>
                    <h2>Floor-Plan</h2>
                    <div style={carouselTwoStyle.floorPlan}>
                    <Image src="https://i.pinimg.com/originals/9c/05/9f/9c059feef8c1ba25dba060caad25e1d3.jpg" alt="Floor-Plan" fluid/>
                    </div>
                </Col>
                <Col style={carouselTwoStyle.col}>
                    <h2>Parking</h2>
                    <div style={carouselTwoStyle.parkingDiv}>
                    <Image style={carouselTwoStyle.parking} src={Parking} alt="Parking" fluid/>
                    </div>
                </Col>
            </Row>
        </Container>
    );
}

export default Carousel2;

